import { Component, h, Element, Prop, Method } from '@stencil/core';

declare const UIkit: any;

@Component({
	tag: 'ks-order-form-modal'
})
export class OrderLoginPrompt {
    @Element() root: HTMLElement;
    @Prop() message: string = "";
    @Prop() back: string = "";

    @Method()
    async Show() {
        const element = document.querySelector("#ks-order-form-modal");
        UIkit.modal(element).show();
    }

    @Method()
    async Hide() {
        const element = document.querySelector("#ks-order-form-modal");
        UIkit.modal(element).hide();
    }

	render() {
		return(
            <div id="ks-order-form-modal" class="uk-modal-full" uk-modal>
                <div class="uk-modal-dialog">
                    <div class="uk-background-cover uk-flex uk-flex-between uk-flex-column" uk-height-viewport>

                        <div class="uk-padding-small uk-flex-auto uk-flex uk-flex-center uk-flex-middle uk-flex-column">
                            <div class="uk-text-center">
                                <p class="ks-text-decorated uk-h1 uk-text-bold">{this.message}</p>
                                <button onClick={()=>this.Hide()} class="uk-button uk-button-danger uk-margin-small-top" style={{padding: "5px 20px", fontSize: "16px"}}>
                                    {this.back}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
